@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Black+Han+Sans&family=Noto+Sans+KR:wght@500&display=swap);
.container {
  perspective: 2000px;
  margin-bottom: 50px;
  margin-top: 50px;
  height: 120px;
}
.seat {
  background-color: rgb(96, 93, 170);
  font-size: 13px;
  text-align: center;
  font-weight: 600;
  height: 20px;
  width: 25px;
  margin: 3px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.seat.selected {
  background-color: rgb(120, 205, 1);
}
.seat.occupied {
  background-color: #fff;
}
.seat:nth-of-type(2) {
  margin-right: 18px;
}
.seat:nth-last-of-type(2) {
  margin-left: 18px;
}
.seat:not(.occupied):hover {
  cursor: pointer;
  transform: scale(1.2);
}
.showcase .seat:not(.occupied):hover {
  cursor: default;
  transform: scale(1);
}
.showcase {
  padding: 5px 10px;
  border-radius: 5px;
  color: #777;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}
.showcase li {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
}
.showcase li small {
  margin-left: 2px;
}
.row {
  display: flex;
  justify-content: center;
}
.screen {
  background-color: #fff;
  height: 70px;
  width: 80%;
  margin: 0 auto;
  margin-bottom: 30px;
  transform: rotateX(-45deg);
  box-shadow: 0 3px 10px rgba(255, 255, 255, 0.7);
}
p.text {
  margin: 5px 0;
}
p.text span {
  color: #6feaf6;
}

.MuiTextField-root{
  color: red;
}

* {
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
  color: #ffffff;
}
body,
html,
.App,
#root,

.auth-wrapper {
  width: 100%;
  height: 100%;
}
.navbar-light {
  background-color: rgba(20, 20, 20, 0.8);
  z-index: 10;
  box-shadow: 0px 1px 5px 2px rgba(0, 0, 0, 0.8);
}
.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("https://assets.nflxext.com/ffe/siteui/vlv3/c385496a-2ed6-4081-9783-49a1ac8a5bbf/7e9484b8-19af-4bcc-8956-501c4b2165fc/KR-ko-20200629-popsignuptwoweeks-perspective_alpha_website_large.jpg");
  background-size: cover;
  background-position: center center;
  min-height: 100vh;
  font-weight: 400;
  font-family: "Noto Sans KR", sans-serif;
}
.auth-inner {
  width: 450px;
  margin: auto;
  background: rgba(0, 0, 0, 0.75);
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.form-control-radio:focus {
  border-color: #167bff;
  box-shadow: none;
  font-size: 30px;
}
.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}
.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}
.custom-control-label {
  font-weight: 400;
}
.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}
.forgot-password a {
  color: #167bff;
}


